'use client';
import React, { useState, useEffect } from 'react';

export default function DailyUpdatesPage() {
  // List of updates
  const [updates, setUpdates] = useState([]);
  const [loading, setLoading] = useState(true);

  // Form states
  const [date, setDate] = useState('');
  const [userName, setUserName] = useState('');
  const [taskTitle, setTaskTitle] = useState('');

  // Fetch updates on mount
  useEffect(() => {
    fetchUpdates();
  }, []);

  const fetchUpdates = async () => {
    try {
      const response = await fetch('http://localhost:5000/api/daily-updates');
      const data = await response.json();
      setUpdates(data);
      setLoading(false);
    } catch (error) {
      console.error('Error fetching updates:', error);
      setLoading(false);
    }
  };

  // Handle form submission
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      // Prepare the body data
      const newData = {
        date, // "YYYY-MM-DD" (string)
        userName, // "Ajmal" (example)
        tasks: [
          {
            title: taskTitle,
            status: 'In Progress', // or any default
            comments: '',
          },
        ],
      };

      const res = await fetch('http://localhost:5000/api/daily-updates', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(newData),
      });

      if (!res.ok) {
        throw new Error(`Request failed with status ${res.status}`);
      }

      // Get the created doc
      const createdUpdate = await res.json();
      console.log('Created:', createdUpdate);

      // Reset form
      setDate('');
      setUserName('');
      setTaskTitle('');

      // Reload updates to show the newly added entry
      fetchUpdates();
    } catch (error) {
      console.error('Error creating daily update:', error);
    }
  };

  // Loading state
  if (loading) {
    return <p>Loading daily updates...</p>;
  }

  return (
    <main style={{ padding: '1rem' }}>
      <h1>Daily Updates</h1>

      {/* Form to create a new daily update */}
      <form onSubmit={handleSubmit} style={{ marginBottom: '2rem' }}>
        <div style={{ marginBottom: '1rem' }}>
          <label>Date: </label>
          <input
            type='date'
            value={date}
            onChange={(e) => setDate(e.target.value)}
            required
          />
        </div>

        <div style={{ marginBottom: '1rem' }}>
          <label>User Name: </label>
          <input
            type='text'
            value={userName}
            onChange={(e) => setUserName(e.target.value)}
            required
          />
        </div>

        <div style={{ marginBottom: '1rem' }}>
          <label>Task Title: </label>
          <input
            type='text'
            value={taskTitle}
            onChange={(e) => setTaskTitle(e.target.value)}
            required
          />
        </div>

        <button type='submit'>Add Daily Update</button>
      </form>

      {/* Display existing updates */}
      {updates.length === 0 ? (
        <p>No updates found.</p>
      ) : (
        <ul>
          {updates.map((update) => (
            <li key={update._id} style={{ marginBottom: '1rem' }}>
              <strong>User:</strong> {update.userName} <br />
              <strong>Date:</strong>{' '}
              {new Date(update.date).toLocaleDateString()}
              <br />
              <strong>Tasks:</strong>
              <ul>
                {update.tasks.map((task, idx) => (
                  <li key={idx}>
                    {task.title} â€” {task.status}
                    {task.comments && ` (Comments: ${task.comments})`}
                  </li>
                ))}
              </ul>
            </li>
          ))}
        </ul>
      )}
    </main>
  );
}
